.icon-container {
 
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #d3d3d3;
  box-shadow: -1px 0 0 #d3d3d3;
  border-left: none;
  background: #fff;
  border-radius: 0;
 
  &:focus {
    z-index: 3;
  }

  &__icon {
    background-repeat: no-repeat;
    background-size: contain;
    width: 16px;
    height: 16px;

    &_shopping-cart {
      background-image: url(../img/Shopping-cart.svg);
    }

    &_favorites {
      background-image: url(../img/like.svg);
    }
  }
}
